import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_DELETE_TABLE = Symbol('TABLE_DELETE_TABLE');

export function deleteTable(table: Table) {
  return {
    type: TABLE_DELETE_TABLE,
    payload: {
      node: table,
    },
  };
}

interface TableDeleteTablePayload {
  node: Table;
}

interface TableDeleteTableAction {
  type: typeof TABLE_DELETE_TABLE;
  payload: TableDeleteTablePayload;
}

export function isTableDeleteTableAction(
  action: Action,
): action is TableDeleteTableAction {
  return action.type === TABLE_DELETE_TABLE;
}